html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.pinup-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000000;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.bg-yellow {
  background-color: #fef46d;
}

.bg-green {
  background-color: #4cb8b3;
}

.header {
  background-color: #4cb8b3;
}
.header .header-content {
  padding: 2.5rem 0;
}
@media (min-width: 768px) {
  .header .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: #ffffff;
  margin-bottom: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fixed {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem;
}

.nav-principal {
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.nav-principal a {
  color: #ffffff;
  font-size: 2rem;
}
.nav-principal a:hover {
  color: #fef46d;
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#9e37f2+63,ffffff+99&0.7+0,1+0,0.7+0,0.7+100,0+100,1+100,0.7+100 */
  background: -moz-linear-gradient(-45deg, rgba(158, 55, 242, 0.7) 0%, rgba(158, 55, 242, 0.7) 63%, rgba(255, 255, 255, 0.7) 99%, rgba(255, 255, 255, 0.7) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(158, 55, 242, 0.7) 0%, rgba(158, 55, 242, 0.7) 63%, rgba(255, 255, 255, 0.7) 99%, rgba(255, 255, 255, 0.7) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(158, 55, 242, 0.7) 0%, rgba(158, 55, 242, 0.7) 63%, rgba(255, 255, 255, 0.7) 99%, rgba(255, 255, 255, 0.7) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#b39e37f2", endColorstr="#b3ffffff",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  width: 100%;
}

.video-content {
  display: flex;
  height: 100%;
  position: relative;
  z-index: 1;
}
.video-content h2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-size: 6rem;
  color: #ffffff;
}
.video-content h2 span {
  font-size: 2rem;
  background-color: #F53756;
  padding: 1rem 2rem;
}

.about-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.festival-information {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.festival-information h2 {
  font-size: 3rem;
}
.festival-information .date {
  font-weight: bold;
  color: #F53756;
}
.festival-information .text {
  line-height: 1.5;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  color: #ffffff;
  text-align: center;
}
.lineup .day {
  font-weight: bold;
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .scenarios-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.scenarios-container .scenario-name {
  padding-top: 2.5rem;
  color: #000000;
  font-size: 2rem;
  font-weight: 300;
}
.scenarios-container .scenario-name:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.rock .scenario-name:after {
  background-image: url("../img/icono_rock.png");
}

.edm .scenario-name:after {
  background-image: url("../img/icono_edm.png");
}

.calendar {
  padding: 0;
  list-style: none;
  margin-bottom: 0;
}
.calendar li {
  font-weight: 700;
  padding: 2.5rem;
}
.calendar li span {
  font-weight: 400;
}

.bg-green .calendar li:nth-child(2n+1) {
  background-color: #3b9691;
}

.bg-yellow .calendar li:nth-child(odd) {
  background-color: #fef03a;
}

.gallery {
  margin: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  padding: 0;
  list-style: none;
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.5rem;
  }
}
@media (min-width: 1200px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2.5rem;
  }
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center;
}
.overlay p {
  width: 5rem;
  height: 5rem;
  color: #ffffff;
  font-weight: bold;
  background-color: #000000;
  display: grid;
  font-size: 3rem;
  place-items: center;
  border-radius: 50%;
}
.overlay p:hover {
  cursor: pointer;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  color: #ffffff;
  text-align: center;
}

@media (min-width: 768px) {
  .tickets-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.tickets-container p {
  color: #ffffff;
  text-align: center;
  font-size: 3rem;
  margin-bottom: 2rem;
}

.pass {
  width: 100%;
  border-spacing: 0;
}
.pass thead {
  background-color: #F53756;
}
.pass thead th {
  padding: 1.25rem;
  color: #ffffff;
  font-weight: 300;
  font-size: 2rem;
  text-transform: uppercase;
}
.pass tbody {
  background-color: #ffffff;
}
.pass tbody td {
  padding: 1.25rem;
  border-bottom: 1px solid #e1e1e1;
}
.pass tbody tr:last-of-type td {
  border-bottom: none;
}
.pass tfoot {
  background-color: #752f97;
}
.pass tfoot td {
  color: #ffffff;
  text-align: center;
  font-size: 3rem;
  padding: 1.25rem 0;
}

.footer {
  background-color: #F53756;
  text-align: center;
  padding: 1.25rem 0;
}
.footer p {
  color: #ffffff;
  margin: 0;
}